.stage {
	&__header {
		display: flex;
		justify-content: flex-end;
	}

	&__body {
		height: 758px;
		background-image: url(/img/top.png);
		background-repeat: no-repeat;
		background-position: right 39px bottom;
	}

	&__title {
		margin: 163px 0 80px;
		max-width: calc(100% - 516px);
	}

	&__button {
		max-width: 340px;
	}
}

@media (min-width: $desktop-sm-min) and (max-width: $desktop-sm-max) {

}

@media (max-width: $tablet-max) {
	.stage {
		&__header {
			justify-content: space-between;
		}

		&__site {
			margin-right: 0;
		}

		&__body {
			height: 544px;
			background-repeat: no-repeat;
			background-position: center bottom 86px;
			background-size: 232px 296px;
			position: relative;
		}

		&__title {
			margin: 110px 0 0;
			max-width: none;
		}

		&__button {
			position: absolute;
			max-width: calc(100% - 32px);
			width: 100%;
			left: 16px;
			bottom: 16px
		}
	}
}
