.services {
	&__body {
		display: flex;
	}

	&__menu {
		width: 32.3529%;
		margin-right: 1.4706%;
		flex-shrink: 0;
	}

	&__content {
		max-width: 785px;

		h2 {
			margin: 56px 0 24px;
			&:first-child {
				margin-top: 0;
			}
		}
	}
}

@media (max-width: $tablet-max) {
	.services {
		&__body {
			display: block;
		}

		&__menu {
			display: none;
		}

		&__content {
			max-width: none;

			h2 {
				margin: 40px 0 16px;
				&:first-child {
					margin-top: 0;
				}
			}
		}
	}
}
