// Links
a {
	color: #55BDF6;
	text-decoration: none;

	&:hover {
		text-decoration: none;
		color: #505050;
	}
}

// headers
h1, .h1,
h2, .h2 {
	font-family: $headers-font-family;
	font-weight: $headers-font-weight;
	margin-top: 0;
	line-height: 1.21875em;
}

h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	font-family: $headers-font-family;
	font-weight: $headers-font-weight;
	margin-top: 0;
}

h1, .h1 {
	font-size: 64px;
}

h2, .h2 {
	font-size: 40px;
}


@media (max-width: #{$desktop-sm-max}) {
	h1, .h1 {
		font-size: 52px;
	}
}

@media (max-width: $tablet-max) {
	h1, .h1 {
		font-size: 28px;
	}
	h2, .h2 {
		font-size: 24px;
	}
}

p, .paragraph {
	margin-top: 0;
	margin-bottom: 1.25em;
}

@media (max-width: $mobile-max) {
	.paragraph {

	}
}

.color-light {
	color: $color-light;
}

.nowrap {
	white-space: nowrap;
}
