.includes {
	&__title {
		margin-bottom: 60px;
	}

	&__body {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		width: 25%;
		margin:  0 0 60px;
	}
}

@media (max-width: $tablet-max) {
	.includes {
		&__title {
			margin-bottom: 30px;
		}

		&__item {
			width: 50%;
			margin:  0 0 30px;
		}
	}
}
