.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;

	&__email-link {
		color: inherit;
		font-weight: 500;
	}

	&__links {
		width: 41%;
		display: flex;
		justify-content: space-between;
		opacity: 1;
		transition: opacity 0.2s ease-in-out;
	}

	&__consult {
		width: 41%;
		text-align: right;
		pointer-events: none;
		opacity: 0;
		transition: opacity 0.2s ease-in-out;
		position: absolute;
		right: 0;
	}

	&__logo {
		padding-left: 40px;
	}

	&__site {
		font-weight: 500;
	}

	&__lang {
		padding-right: 40px;
	}

	svg {
		display: block;
	}

	&_fixed {
		.header__links {
			opacity: 0;
		}
	}

	&_consult {
		.header__consult {
			opacity: 1;
			pointer-events: all;
		}
	}

	&_sub {
		display: flex;
		justify-content: flex-end;
	}
}

@media (max-width: $tablet-max) {
	.header {
		flex-wrap: wrap;
		justify-content: flex-start;

		&__email {
			display: none;
		}

		&__logo {
			order: 10;
			width: 100%;
			padding-left: 11px;

			svg {
				width: 120px;
			}
		}

		&__site {
			padding-left: 11px;
			margin-right: 0;
		}

		&__lang {
			padding-right: 0;
		}

		&__consult {
			display: none;
		}

		&__links {
			width: 100%;
			position: absolute;
			top: -40px;
		}

		&_sub {
			justify-content: space-between;
		}
	}
}
