.about {
	display: flex;

	&__title {
		width: 32.3529%;
		margin-right: 1.4706%;
		flex-shrink: 0;
	}

	&__text {
		font-size: 40px;
		line-height: 1.4em;
	}
}

@media (max-width: $tablet-max) {
	.about {
		display: block;

		&__title {
			width: 100%;
			margin-right: 0;
		}

		&__text {
			font-size: 16px;
			line-height: 1.5em;
		}
	}
}
