.consultation {
	&__title {
		margin-bottom: 64px;
	}

	&__body {
		display: flex;
	}

	&__form {
		width: 48.5294%;
		margin-right: 2.2059%;
		flex-shrink: 0;
	}

	&__text {
		font-size: 32px;
		line-height: 1.25em;
		padding-top: 113px;
	}

	&__phone {
		color: inherit;
	}
}

@media (max-width: $tablet-max) {
	.consultation {
		&__title {
			margin-bottom: 24px;
			max-width: 270px;
		}

		&__body {
			display: flex;
			flex-direction: column;
		}

		&__form {
			width: 100%;
			margin-right: 0;
			order: 10;
		}

		&__text {
			font-size: 24px;
			line-height: 1.5em;
			padding-top: 0;
			order: 1;
		}

		&__phone {
			color: inherit;
		}
	}
}
