@mixin button-variant($color, $background, $border, $color-hover, $background-hover, $border-hover, $color-active, $background-active, $border-active) {
	color: $color;
	background: $background;
	border-color: $border;

	&:focus {
		outline: $outline;
	}

	&:hover,
	&.button_hover,
	&.button_focus {
		text-decoration: none;
		background: $background-hover;
		border-color: $border-hover;
		color: $color-hover;
	}

	&:active,
	&.button_active {
		color: $color-active;
		background: $background-active;
		border-color: $border-active;

		&:hover,
		&:focus,
		&.button_hover,
		&.button_focus {
			color: $color-active;
			background: $background-active;
			border-color: $border-active;
		}
	}

	&.button_disabled,
	&[disabled] {
		pointer-events: none;

		&,
		&:hover,
		&:focus,
		&:active,
		&.button_focus,
		&.button_active {
			color: $btn-disabled-color;
			background: $btn-disabled-background;
			border-color: $btn-disabled-border-color;
		}
	}
}

// Button sizes
@mixin button-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $height) {
	padding: $padding-vertical $padding-horizontal;
	font-size: $font-size;
	line-height: $line-height;
	height: $height;
	min-width: calc(1em + #{$padding-horizontal}*2);
}

.button {
	display: inline-block;
	min-width: calc(1em + $btn-md-padding-horizontal*2);
	font-family: $btn-font-family;
	font-weight: $btn-font-weight;
	text-align: center;
	text-decoration: none;
	vertical-align: middle;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	touch-action: manipulation;
	cursor: pointer;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	background-image: none;
	border-width: 1px;
	border-style: solid;
	border-radius: $btn-md-border-radius;

	@include button-variant(
		$btn-default-color, $btn-default-background, $btn-default-border-color,
		$btn-default-color-hover, $btn-default-background-hover, $btn-default-border-color-hover,
		$btn-default-color-hover, $btn-default-background-hover, $btn-default-border-color-hover
	);
	@include button-size($btn-md-padding-vertical,  $btn-md-padding-horizontal, $btn-md-font-size, $btn-md-line-height, $btn-md-height);
	transition: background 0.2s, transform 0.01s, border-color 0.2s;
}

.button_size_sm {
	@include button-size($btn-sm-padding-vertical,  $btn-sm-padding-horizontal, $btn-sm-font-size, $btn-sm-line-height, $btn-sm-height);
}

.button_block {
	display: block;
	width: 100%;
}

@media (max-width: $tablet-max) {
	.button {
		min-width: calc(1em + $btn-mobile-md-padding-horizontal*2);
		border-width: 1px;
		border-style: solid;
		border-radius: $btn-mobile-md-border-radius;

		@include button-size($btn-mobile-md-padding-vertical,  $btn-mobile-md-padding-horizontal, $btn-mobile-md-font-size, $btn-mobile-md-line-height, $btn-mobile-md-height);
	}
}
