.for-whom {
	&__title {
		margin-bottom: 64px;
	}

	&__body {
		display: flex;
		margin: 0 -10px;
	}

	&__item {
		margin: 0 10px;
		width: calc(50% - 20px);
	}
}

@media (max-width: $tablet-max) {
	.for-whom {
		&__title {
			margin-bottom: 16px;
		}

		&__body {
			display: block;
			margin: 0 -8px;
		}

		&__item {
			margin: 24px 0 0;
			width: 100%;

			&:first-child {
				margin-top: 0;
			}
		}
	}
}
