.footer {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 60px 0;

	&__copy {
		a {
			color: inherit;
		}
	}
}

@media (max-width: $tablet-max) {
	.footer {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: 0 0 16px;

		svg {
			max-width: 100%;
		}

		&__dz {
			width: 120px;
			order: 1;
			margin-right: 60px;
			margin-bottom: 45px;
		}

		&__usabilitylab {
			width: 120px;
			order: 2;
			margin-bottom: 45px;
		}

		&__copy {
			order: 3;
		}

		&__website {
			order: 4;
		}
	}
}
