.services-menu {
	list-style: none;
	line-height: 1.6em;
	margin: 0;
	padding: 0;

	&__item {
		margin-bottom: 16px;
	}

	&__link {
		color: $color-regular;
		font-weight: 600;

		&_active {
			padding-left: 48px;
			position: relative;

			&::before {
				content: ' ';
				display: block;
				width: 32px;
				height: 2px;
				background: #55BDF6;
				position: absolute;
				top: 50%;
				transform: translate(0, -50%);
				left: 0;
			}
		}
	}
}
