.page {
	padding: 21px 40px;

	&__header {
		position: absolute;
		width: 100%;
		left: 0;
		top: 27px;
		padding: 28px 40px;
		z-index: 100;

		&_fixed {
			position: fixed;
			top: 0;
		}

		&_blur {
			&.page__header_fixed {
				background: rgba(255, 255, 255, 0.64);
				backdrop-filter: blur(40px);
			}
		}

		&_fade {
			&.page__header_fixed {
				background: rgba(255, 255, 255, 0.95);
				box-shadow: 0 2px 4px 0 rgba(0,0,0,.1);
			}
		}

		&_sub {
			width: 50%;
			left: 50%;
			top: 33px;
		}
	}
}

@media (max-width: $tablet-max) {
	.page {
		padding: 8px 16px;
		z-index: 10;

		&__header {
			padding: 18px 16px;
			top: 50px;

			&_fixed {
				top: 0;
			}

			&_sub {
				width: 100%;
				top: 6px;
			}
		}
	}
}
