.artifact {
	&__body {
		height: 480px;
		background-image: url(/img/examples.png);
		background-repeat: no-repeat;
		background-position: right 62px top;
		background-size: 479px 480px;

		@media
		only screen and (-webkit-min-device-pixel-ratio: 2),
		only screen and (min-resolution: 192dpi) {
			background-image: url(/img/examples@2x.png);
		}
	}

	&__title {
		max-width: calc(100% - 561px);
		width: 590px;
		margin: 80px 0 64px;
		font-size: 32px;
		line-height: 1.25em;
		font-weight: 400;
	}

	&__button {
		max-width: 310px;
	}
}

@media (max-width: $tablet-max) {
	.artifact {
		&__body {
			position: relative;
			height: 442px;
			background-image: url(/img/examples-mobile.png);
			background-position: center bottom 82px;
			background-size: 240px 264px;

			@media
			only screen and (-webkit-min-device-pixel-ratio: 2),
			only screen and (min-resolution: 192dpi) {
				background-image: url(/img/examples-mobile@2x.png);
			}
		}

		&__title {
			max-width: none;
			width: 100%;
			margin: 8px 0 0;
			font-size: 16px;
			line-height: 1.5em;
		}

		&__button {
			position: absolute;
			left: 16px;
			bottom: 16px;
			width: calc(100% - 32px);
			max-width: none;

			&::after {
				content: '';
				position: absolute;
				top: -94px;
				left: 0;
				width: 100%;
				height: 80px;
				background: linear-gradient(0deg, #F2F3F7 0%, rgba(242, 243, 247, 0.828124) 14.48%, rgba(242, 243, 247, 0) 31.5%);
			}
		}
	}
}
